import React from 'react';

const styles = {
    wrapper: {
        position: 'absolute',
        width: '100%',
        zIndex: '100',
        bottom: '0',
        textAlign: 'center',
    },
    btn: {
        width: '30px',
        height: '30px',
        cursor: 'pointer',
        userSelect: 'none',
        position: 'absolute',
        bottom: '0',
        fontSize: '2rem',
        color: 'var(--alt-blue)',
    },
    left: {
        left: '40%',
    },
    right: {
        right: '40%',
    },
};

export default function Buttons(props) {
    const prevBtnStyle = Object.assign({}, styles.btn, styles.left);
    const nextBtnStyle = Object.assign({}, styles.btn, styles.right);
    const { index, total, loop, prevHandler, nextHandler } = props;
    return (
        <div style={styles.wrapper}>
            {(loop || index !== 0) && (
                <div style={prevBtnStyle} onClick={prevHandler}>
                    
                </div>
            )}
            {(loop || index !== total - 1) && (
                <div style={nextBtnStyle} onClick={nextHandler}>
                    
                </div>
            )}
        </div>
    );
}
